.header {
    background: #347474;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    justify-content: space-around;
  
  }
.header-contents {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    row-gap: 3rem;
   
}

.shop-name {

    /* width: 589px;
    height: 50px;
    left: calc(50% - 589px/2 - 248.5px);
    top: calc(50% - 50px/2 - 1610.5px);  */
    font-style: normal;
    font-weight: 500;
    font-size: 50px;
    line-height: 100%;
    color: #f1f5f5;
    font-size: 3rem;
    margin-right: 7rem;
    padding: 1rem;
}

.basket {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: end;
    margin-right: 2rem;
    padding: 1rem;
}
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    font-size: 2rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(7, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
 
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }
  
  .nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover {
    background-color: #009f4f;
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 1200px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #009f4f;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      justify-content: start;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    /* .nav-links:hover {
      background-color: #59bd8b;
      border-radius: 0;
    } */
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #59bd8b;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #009f4f;
      transition: 250ms;
    }
  
    .login-btn {
      display: none;
    }
  }
  